* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  
  .App {
    font-family: Arial, sans-serif;
  }
  
  .main-container {
    display: flex;
  }
  
  .content {
    padding: 20px;
    background-image: url('./assets/Fondopaguina.png');
    background-size:auto;
    background-attachment: fixed;
    background-position: center;
    color:#ffffff;
    height: calc(79vh - 80px);
    display: flex;
    flex-direction:row;
    justify-content: flex;
    align-items: center;
  }
  
  
  .content h1 {
    font-size: 2.8rem;
    font-weight: bold;
    margin-bottom: 9px;
  }
  
  .content p {
    font-size: 1.2rem;
    line-height: 1.9;
  }



  .more-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: white;
    background-color: 	#8F00FF;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .more-button:hover {
    background-color: #1d0032;
  }
  
  .more-info {
    margin-top: 15px;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 15px;
    border-radius: 5px;
  }
  

  .topbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    color: rgb(0, 0, 0);
    padding: 10px 20px;
  }
  
  .topbar-left {
    display: flex;
    align-items: center;
  }
  
  .topbar-logo {
    width: 180px;
    height: auto;
    margin-right: 3px;
  }
  
  .topbar h1 {
    font-size: 1.2rem;
    font-weight: bold;
    margin: 0;
  }
  
  .topbar-right ul {
    display: flex;
    list-style-type: none;
    margin: 6;
  }
  
  .topbar-right ul li {
    margin: 0 15px;
  }
  
  .topbar-right ul li a {
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-weight: bold;
    font-size: 1rem;
  }
  
  
  .file-upload {
    margin-top: 100px;
  }
  