/* FileUpload.css */

.file-upload {
    background-color: #ffffff1e;
    border-radius: 10px;
    padding: 40px;
    max-width: 400px;
    text-align: center;
    margin: 40px auto; /* Centrar el cuadro en la página */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .file-upload h1 {
    font-size: 2rem;
    color: #333;
    text-align: center;
    margin-bottom: 10px; /* Espaciado inferior para que el cuadro esté más abajo */
  }
  
  .file-upload h3 {
    font-size: 1.5rem;
    color: #ffffff;
    margin-bottom: 10px;
  }
  
  .file-upload input[type="file"] {
    display: block;
    margin: 10px auto;
  }
  
  .upload-button {
    background-color: #8F00FF;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
  }
  
  .upload-button:hover {
    background-color: #25004d;
  }
  